import React from 'react';
import { Frame } from './Main.styles';
import { TwitchPlayer } from 'react-twitch-embed';

export const Main = () => {
  return (
    <Frame>
      <TwitchPlayer
        channel="asylatv"
        id="asylatv"
        theme="dark"
        width="100%"
        height="100%"
        muted
        onVideoPause={() => console.log(';_;')}
      />
    </Frame>
  );
};
